/***************Button group********************/

.btn-group{
  width: 100%;
  display: flex;
  grid-gap: 0.5em;
  gap: 0.5em;
  justify-content: center;
  align-items: center;
}

.btn-group__item {
  flex: 1 1;
  height: 45px;
  font-size: minmax(0.5rem,1rem);
  cursor: pointer;
  border: none;
  outline: none;
  background: transparent;
  color: rgb(236, 236, 236);
  font-family: 'Times New Roman', Times, serif;
  font-weight: 600;
  position: relative;
  transition: all 0.5s;
  z-index: 1;
 }
 
 .btn-group__item::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 5px;
  height: 100%;
  background-color: #3D434D;
  /* background-color: #203231; */
  z-index: -1;
  transition: all 0.5s;
 }
 
 .btn-group__item:hover::before {
  width: 100%;
 }
 
 .btn-group__item:hover {
  color: #0C121F;
 }
 

