 }
 
 .tutorial-btn::before {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: #38615f;
  -webkit-transform: translateX(-100%);
          transform: translateX(-100%);
  transition: all .3s;
  z-index: -1;
 }
 
 .tutorial-btn:hover::before {
  -webkit-transform: translateX(0);
          transform: translateX(0);
 }

