/**************tutorial button menu ********/

#split-button-menu{
  background-color: #38615f !important;
}

#split-button-menu * {
  font-size: 0.8em;
  text-transform: uppercase;
  text-align: center;
  font-weight: bold;
  border-radius: 2px;
  position: relative;
  color: #122726 !important;
  text-decoration: none;
  transition: 0.3s ease all;
  z-index: 1;
 }

#split-button-menu *:before {
  transition: 0.5s all ease !important;
  position: absolute;
  top: 0;
  left: 50%;
  right: 50%;
  bottom: 0;
  opacity: 0;
  content: '';
  background-color: #122726 !important;
  z-index: -1;
 }
 
 #split-button-menu *:hover, #split-button-menu *:focus {
  color: white !important;
 }
 
 #split-button-menu *:hover:before, #split-button-menu *:focus:before {
  transition: 0.5s all ease !important;
  left: 0;
  right: 0;
  opacity: 1;
 }
 
 #split-button-menu *:active {
  -webkit-transform: scale(0.9) !important;
          transform: scale(0.9) !important;
 }

