.sort-btn {
  --color: #097cb1;
  --color2: rgb(10, 25, 30);
  padding: 0.5em 1em;
  background-color: transparent;
  border-radius: 6px;
  border: .3px solid var(--color);
  transition: .5s;
  position: relative;
  overflow: hidden;
  cursor: pointer;
  z-index: 1;
  font-weight: 300;
  font-size: 17px;
  font-family: 'Roboto', 'Segoe UI', sans-serif;
  text-transform: uppercase;
  color: var(--color);
}

.sort-btn::after,
.sort-btn::before {
  content: '';
  display: block;
  height: 100%;
  width: 100%;
  -webkit-transform: skew(90deg) translate(-50%, -50%);
          transform: skew(90deg) translate(-50%, -50%);
  position: absolute;
  inset: 50%;
  left: 25%;
  z-index: -1;
  transition: .5s ease-out;
  background-color: var(--color);
}

.sort-btn::before {
  top: -50%;
  left: -25%;
  -webkit-transform: skew(90deg) rotate(180deg) translate(-50%, -50%);
          transform: skew(90deg) rotate(180deg) translate(-50%, -50%);
}

.sort-btn:hover::before {
  -webkit-transform: skew(45deg) rotate(180deg) translate(-50%, -50%);
          transform: skew(45deg) rotate(180deg) translate(-50%, -50%);
}

.sort-btn:hover::after {
  -webkit-transform: skew(45deg) translate(-50%, -50%);
          transform: skew(45deg) translate(-50%, -50%);
}

.sort-btn:hover {
  color: var(--color2);
}

.sort-btn:active {
  -webkit-filter: brightness(.7);
          filter: brightness(.7);
  -webkit-transform: scale(.98);
          transform: scale(.98);
}

