.hero {
    text-align: center;
    padding-top: 48px;
    padding-bottom: 88px
}

.hero-copy {
    position: relative;
    z-index: 1
}

.hero-cta {
    margin-bottom: 40px
}

.hero-figure {
    position: relative
}

.hero-figure svg {
    width: 100%;
    height: auto
}

.hero-paragraph{
    font-size: 1.2em;
}

.hero-figure::before,
.hero-figure::after {
    content: '';
    position: absolute;
    background-repeat: no-repeat;
    background-size: 100%
}

.has-animations .hero-figure::before,
.has-animations .hero-figure::after {
    opacity: 0;
    transition: opacity 2s ease
}

.anime-ready .has-animations .hero-figure::before,
.anime-ready .has-animations .hero-figure::after {
    opacity: 1
}

.hero-figure::before {
